import React, { useEffect, useState } from 'react';
import { acceptableGuesses, answers } from '../../words';
import { Color, Guess } from '../../interfaces/guess.interface';
import GuessHistory from '../History';
import { CornerButton, CornerCountdown, GameBoard, GameInput, HeaderText } from './game.style';
import { getDay, getTodaysWord } from '../../utils/word-picker';
import { CookieKey, loadCookie, storeCookie } from '../../utils/cookies';
import Keyboard from '../Keyboard';
import WordInput from '../WordInput';
import ShareModal from '../Share';
import { MdHelpOutline, MdShare } from 'react-icons/md';
import Help from '../Help';
import { sendEvent } from '../../utils/event';
import Countdown from 'react-countdown';

interface DailyChallengeHistory {
  day: number;
  noGuesses: number;
}

const Game = () => {
  const [answer, setAnswer] = useState<string>('');
  const [guess, setGuess] = useState<string>('');
  const [guessHistory, setGuessHistory] = useState<Guess[]>([]);
  const [inputError, setInputError] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [day, setDay] = useState(0);
  const [showKeyboard, setShowKeyboard] = useState<boolean>(true);
  const [showShare, setShowShare] = useState(false);
  const [showHelp, setShowHelp] = useState<boolean>(false);

  useEffect(() => {
    setDay(getDay());
    checkDailyWord();
  }, []);

  useEffect(() => {
    setInterval(() => {
      if (getDay() !== loadCookie<number>(CookieKey.CurrentDay)) {
        setupDailyChallenge();
      }
    }, 5000);
  }, []);

  useEffect(() => {
    if (!guessHistory.length) {
      return;
    }
    storeCookie(CookieKey.GuessHistory, guessHistory);
  }, [guessHistory]);

  useEffect(() => {
    if (!loadCookie<boolean>(CookieKey.RepeatVisit)) {
      setShowHelp(true);
      storeCookie<boolean>(CookieKey.RepeatVisit, true);
    }
  }, []);

  const setupDailyChallenge = () => {
    const day = getDay();
    setDay(day);
    storeCookie<number>(CookieKey.CurrentDay, day);
    newGame(getTodaysWord());
  };

  const checkDailyWord = () => {
    const todaysWord = getTodaysWord();
    if (answer === todaysWord) {
      return;
    }

    const day = getDay();
    const currentlyCompleting = loadCookie<number>(CookieKey.CurrentDay);
    if (day !== currentlyCompleting) {
      setupDailyChallenge();
      return;
    }

    const dailyChallengeHistory = loadCookie<DailyChallengeHistory[]>(
      CookieKey.DailyChallengeHistory,
    );

    setupGame(todaysWord, loadCookie<Guess[]>(CookieKey.GuessHistory) || [], guess);
    if (
      dailyChallengeHistory &&
      dailyChallengeHistory.length &&
      dailyChallengeHistory[0].day === day
    ) {
      setIsCorrect(true);
      setGuess(todaysWord);
    }
  };

  const newGame = (answer: string) => {
    setupGame(answer, []);
    storeCookie(CookieKey.GuessHistory, []);
  };

  const setupGame = (answer: string, guessHistory: Guess[], guess = '') => {
    setAnswer(answer);
    guessHistory.forEach((guess) => {
      if (!guess.letterColors) {
        guess.letterColors = guess.word.split('').map(() => Color.White);
      }
    });
    setGuessHistory(guessHistory);
    setGuess(guess);
    setIsCorrect(false);
  };

  const addGuess = (guess: Guess) => {
    guess.letterColors = guess.word.split('').map(() => Color.White);
    setGuessHistory([...guessHistory, guess]);
  };

  const compareAnswer = () => {
    if (!acceptableGuesses.includes(guess) && !answers.includes(guess)) {
      setInputError(true);
      return;
    }
    if (guessHistory.length === 0) {
      sendEvent('game_started', { guess });
    }
    sendEvent(`word_guessed`, { guess });

    setInputError(false);
    const incorrect: string[] = [];
    const unmatched: string[] = [];

    Array.from(guess).forEach((character, index) => {
      const ansChar = answer[index];
      if (character !== ansChar) {
        incorrect.push(character);
        unmatched.push(ansChar);
      }
    });

    const correctPos = answer.length - unmatched.length;

    incorrect.forEach((character) => {
      const index = unmatched.indexOf(character);
      if (index !== -1) {
        unmatched.splice(index, 1);
      }
    });

    const incorrectPos = answer.length - correctPos - unmatched.length;

    setGuess('');
    addGuess({ word: guess, correctPos, incorrectPos });
  };

  const checkWord = () => {
    if (guess === answer) {
      setIsCorrect(true);
      addGuess({ word: guess, correctPos: 5, incorrectPos: 0 });

      const dailyChallengeHistory = loadCookie<DailyChallengeHistory[]>(
        CookieKey.DailyChallengeHistory,
      );

      const newHistory = [
        { day, noGuesses: guessHistory.length + 1 },
        ...(dailyChallengeHistory || []),
      ];

      storeCookie(CookieKey.DailyChallengeHistory, newHistory);
      setShowShare(true);
      sendEvent(`completed_game`, { noGuesses: guessHistory.length + 1 });
      return;
    }

    compareAnswer();
  };

  const submit = () => {
    if (guess.length === answer.length && !isCorrect) {
      checkWord();
    }
  };
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [showHelp, showShare]);

  const inputColor = isCorrect ? '#aaffaa' : inputError ? '#ffbbbb' : '#ffffff';
  const woodleNumber = day - 19035;
  const showCornerButton = !showShare && !showHelp;
  const cornerButtonIcon = isCorrect ? <MdShare size="2.5rem" /> : <MdHelpOutline size="2.5rem" />;
  const cornerButtonClick = isCorrect ? () => setShowShare(true) : () => setShowHelp(true);
  return (
    <>
      <GameBoard>
        {isCorrect && (
          <CornerCountdown>
            <Countdown
              daysInHours
              date={(getDay() + 1) * (24 * 60 * 60 * 1000)}
              renderer={(props) => {
                return (
                  <span>
                    {/* eslint-disable-next-line react/prop-types */}
                    Woodle #{woodleNumber + 1}: {props.hours.toString().padStart(2, '0')}:
                    {/* eslint-disable-next-line react/prop-types */}
                    {props.minutes.toString().padStart(2, '0')}:
                    {/* eslint-disable-next-line react/prop-types */}
                    {props.seconds.toString().padStart(2, '0')}
                  </span>
                );
              }}
            />
          </CornerCountdown>
        )}
        {showCornerButton && (
          <CornerButton onClick={cornerButtonClick}>{cornerButtonIcon}</CornerButton>
        )}
        <HeaderText>Woodle #{woodleNumber} </HeaderText>
        <GameInput
          onKeyUp={(e) => {
            if (e.key === 'Enter') {
              submit();
            }
          }}
        >
          <WordInput
            word={guess}
            setWord={setGuess}
            numInputs={5}
            setShowKeyboard={setShowKeyboard}
            backgroundColor={inputColor}
          />
        </GameInput>

        {!!guessHistory.length && (
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <GuessHistory guessHistory={guessHistory} setGuessHistory={setGuessHistory} />
          </div>
        )}
      </GameBoard>
      <Keyboard
        expanded={showKeyboard}
        onToggleExpanded={setShowKeyboard}
        pressLetter={(character: string) => {
          if (guess.length < 5) {
            setGuess(guess + character.toLowerCase());
          }
        }}
        onBackspacePress={() => {
          if (guess.length) {
            setGuess(guess.substring(0, guess.length - 1));
          }
        }}
        onEnterPress={submit}
        guessHistory={guessHistory}
        disabled={isCorrect}
      />
      {showHelp && <Help onDismiss={() => setShowHelp(false)} />}
      {isCorrect && showShare && (
        <ShareModal
          onDismiss={() => setShowShare(false)}
          woodleNumber={woodleNumber}
          guessCount={guessHistory.length}
        />
      )}
    </>
  );
};

export default Game;
